import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/DayOutPackages";
import ProductTable from "components/DayOutPackages/DayOutPackagesTable";
import useProductFilter from "hooks/useProductFilter";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import { Link } from "react-router-dom";
import { DayOutPackagesList } from "services/Api";

const Products = () => {
  const { title, allId, serviceId, handleDeleteMany, handleUpdateMany } =
    useToggleDrawer();

  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    currentPage,
    searchText,
    category,
    setCategory,
    searchRef,
    handleSubmitForAll,
    sortedField,
    setSortedField,
    limitData,
  } = useContext(SidebarContext);

  const { data } = useAsync(() =>
    ProductServices.getAllProducts({
      page: currentPage,
      limit: limitData,
      category: category,
      title: searchText,
      price: sortedField,
    })
  );

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const currency = globalSetting?.default_currency || "$";
  const [pagenumber, setpagenumber] = useState(1);
  const [loading, setLoading] = useState(true);
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [isCheck, setIsCheck] = useState([]);
  const [wholeCount, setwholeCount] = useState({});
  const [AllDayOutPackagesDetailList, setAllDayOutPackagesDetailList] =
    useState([]);
  const handleSelectAll = () => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(propertiesformlocal.map((li) => li._id));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  const { serviceData } = useProductFilter(propertiesformlocal);
  useEffect(() => {
    getAllDayOutPackagesList(pagenumber);
  }, [pagenumber]);

  const getAllDayOutPackagesList = async (pagenumber, search = "") => {
    const DayoutList = await DayOutPackagesList(pagenumber, search);
    console.log(DayoutList.data.data, "DayoutpackagesList");
    setLoading(false);
    setAllDayOutPackagesDetailList(DayoutList?.data?.data);
    setwholeCount(DayoutList?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllDayOutPackagesList(e);
  };

  const handleSearch = (search) => {
    getAllDayOutPackagesList(pagenumber, search);
  };
  return (
    <>
      <PageTitle>{t("Day Out Packages")}</PageTitle>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to="/add-day-out-packages">
                  {" "}
                  <Button
                    // onClick={toggleDrawer}
                    className="w-full rounded-md h-12"
                  >
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Day Out")}
                  </Button>{" "}
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell className="text-left">{"s.no"}</TableCell>
                <TableCell>{t("Property")}</TableCell>
                <TableCell>{t("Package Name")}</TableCell>
                <TableCell>{t("Days")}</TableCell>
                {/* <TableCell>{t("Room")}</TableCell> */}
                {/* <TableCell>{t("checkin Date")}</TableCell> */}
                <TableCell>{t("Check In/Out Timing")}</TableCell>
                <TableCell>{t("Activities")}</TableCell>
                <TableCell>{t("Badge")}</TableCell>
                <TableCell>{t("Buffets")}</TableCell>
                <TableCell>{t("Price")}</TableCell>
                <TableCell>{t("AVAILABILITY")}</TableCell>
                <TableCell>{t("Blocked Date")}</TableCell>
                {/* <TableCell>{t("Itenary")}</TableCell> */}
                {/* <TableCell
                  className=""
                  style={{ width: "250px", display: "block" }}
                >
                  {t("Image")}
                </TableCell> */}
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                {
                  <TableCell className="text-right">{t("Action")}</TableCell>
                  /*  <TableCell className="text-center">
                  {t("PublishedTbl")}
                </TableCell>
                <TableCell className="text-right">{t("ActionsTbl")}</TableCell> */
                }
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              isCheck={isCheck}
              products={AllDayOutPackagesDetailList?.dayOutPackages}
              setIsCheck={setIsCheck}
              getAllDayOutPackagesList={getAllDayOutPackagesList}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={AllDayOutPackagesDetailList?.pagination?.total}
              resultsPerPage={AllDayOutPackagesDetailList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
